html,
body {
	margin: 0%;
	padding: 0%;
	overflow-x: hidden;
}

.hover-shadow:hover {
	text-shadow: 0px 0px 25px black;
	transition: .3s;
}

.header {
	width: 100vw;
	height: inherit;
	background-color: black;
	position: fixed;
	z-index: 100;
}

.t-center {
	text-align: center;
}

#mobile_device {
	visibility: hidden;
  }

@media only screen and (max-width: 600px) {
	#mobile_device {
		visibility: visible;
	}
  }